<template>
  <div class="NDT-Page">
    <h1 class="NDT-Page__Header">
      {{ page.name }}
    </h1>
    <div
      class="NDT-Page__Body"
      v-html="page.body"
    />
    <template v-if="page.type">
      <component :is="page.type" />
    </template>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import Tickets from '@/views/Tickets'

  export default {
    name: 'Page',

    components: {
      Tickets
    },

    props: {
      slug: {
        type: String,
        default: null
      }
    },

    computed: {
      ...mapGetters({
        pages: 'pages/pages'
      }),

      page () {
        if (this.slug) {
          const page = this.pages
            .find(({ slug }) => slug === this.slug)

          if (page) {
            return page
          }

          return {
            id: '_404',
            name: '404',
            body: `
              <div class="notfound">
                <h1 class="notfound__title">Siden ble ikke funnet</h1>
                <img class="notfound__img" src="https://d1i137u4q32ft6.cloudfront.net/1538004648/darthvader.jpg">
              </div>
            `
          }
        }
      }
    },

    methods: {
      ...mapActions({
        setPages: 'pages/setPages'
      })
    },

    async mounted () {
      const pages = await this.$api.get('pages')

      this.setPages(pages)
    }
  }
</script>

