  @import "../assets/scss/variables";

  .NDT-Page {
    color: $white;
    border-radius: .25rem;
    background: $light-gray;
    border: 1px solid $dark-gray;
    padding: .5rem 1rem .5rem 1rem;

    &__Header {
      color: $lime;
      padding-bottom: 1rem;
      margin-bottom: 1rem;
      border-bottom: 1px solid $dark-gray;
    }

    /deep/ .notfound {
      &__title {
        text-align: center;
      }

      &__img {
        width: 100%;
      }
    }
  }
</style>
